
// body {background-color: black;}

.app {
    margin-top: 2em;
    padding: 2em;
    width: 65em;
}

.form {
    padding: 2em 0;

    &__input {
        background: transparent;
        border: 0;
        outline: 0;
        width: 20%
    }

    > button {
        margin-top: .5em;
        background: #368fd8;
        color: white;
        padding: .4em 1em;
        border: 0;
        outline: 0;
        border: .02em solid #368fd8;
        cursor: pointer;
    }
}


ul {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
}

li {
    padding: .50em 1em;
    margin-bottom: 1em;
    // background-color: green;
}

.text {
    display: flex;
    align-items: center;
}

.course__course {
    // display: block;
    color: #464646cc;
}

.btn {
    display: flex;
    background: transparent;
    border: 0;
    outline: 0;
    padding: .5em .5em;
    cursor: pointer;
}

.rounded {
    display: block;
    margin-right: .3em;
    border-radius: 50%;
    width: .4em;
    height: .5em;
    background: red;
}

.round {
    background: rgb(13, 241, 13)!important;
}


.btns__btn {
    margin-top: .7em;
    background: #f5f5f5;
    padding: .4em 1em;
    border: 0;
    outline: 0;
    border: .02em solid #ccc;
    color: #464646cc;
    font-size: .75em;

}


.pop {
    position: absolute;
    top: 0 ;
    left: 0;
    margin-bottom: 2em;
    width: 100%;
    height: 3em;
    // display: none;
    display: flex;
    align-items: center;
    padding: 0 6em;
    // background:rgb(13, 241, 13);
}

.succes {
    background:rgb(13, 241, 13); 
}
.error {
    background: red;
}

.server {
    background: #368fd8!important;
}